body {
    height: 100vh;
    margin: 0;
    background: #222222;
    background-image: linear-gradient(145deg, rgba(9, 73, 72, 0.3) 0%, rgba(142, 241, 239, 0.3) 100%);
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
}

.container {
    width: 550px;
    text-align: center;
}