}.footer{grid-area:footer}body{height:100vh;margin:0;background:#222;background-image:linear-gradient(145deg, rgba(9, 73, 72, 0.3) 0%, rgba(142, 241, 239, 0.3) 100%);background-repeat:no-repeat;background-size:cover;overflow:hidden}.container{width:550px;text-align:center}.aside{display:flex;flex-direction:column;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.header{display:flex;flex-direction:row;justify-content:flex-start}.footer{position:absolute;bottom:0;left:50%;margin:0;font-size:1.5rem;color:#fff;margin-bottom:.4rem}.logo_container{margin-top:1rem}#logo{font-family:"Zen Dots",cursive;fill:#cedef3;position:relative}#logo text{text-transform:uppercase;font-size:3.3rem;stroke-width:2;stroke:#cedef3;stroke-dasharray:500;stroke-dashoffset:0;-webkit-animation:stroke 5s linear;animation:stroke 5s linear}@-webkit-keyframes stroke{0%{stroke-dashoffset:0;fill:rgba(206,222,243,0)}40%{stroke-dashoffset:500;fill:rgba(206,222,243,.3)}80%{stroke-dashoffset:1000;fill:logo-06}100%{stroke-dashoffset:1000;fill:#cedef3}}@keyframes stroke{0%{stroke-dashoffset:0;fill:rgba(206,222,243,0)}40%{stroke-dashoffset:500;fill:rgba(206,222,243,.3)}80%{stroke-dashoffset:1000;fill:logo-06}100%{stroke-dashoffset:1000;fill:#cedef3}}#logo:hover{cursor:pointer;fill:none}#logo_figures{margin-right:1rem}#logo_figures:hover{cursor:pointer;fill:none}.figure{margin-top:6rem}@media(max-width: 768px){.figure{margin-top:1rem}}#figure{-webkit-filter:url(#pencil);filter:url(#pencil)}#reload{cursor:pointer}.react-icons{width:30px;height:30px;color:orange;vertical-align:middle;cursor:pointer}css-doodle{position:absolute;bottom:0;right:0;opacity:.3;z-index:-1}
